import React, { Fragment } from "react";
import CommonTable from "../ComTable";
import TdHead from "../TdHead";

const emphasized = true;
const PLTheadCols = [
  { label: '営業収入', key: '営業収入', sortable: true},
  { label: '営業費用', key: '営業費用', sortable: true},
  { label: '営業利益', key: '営業利益', sortable: true, emphasized },
  { label: '営業外収益', key: '営業外収益' },
  { label: '営業外費用', key: '営業外費用' },
  { label: '経常利益', key: '経常利益', sortable: true, emphasized },
  { label: '特別利益', key: '特別利益' },
  { label: '特別損失', key: '特別損失' },
  { label: '税引前', key: '税引前当期利益', emphasized },
  { label: '法人税等', key: '法人税および住民税等' },
  { label: '当期純利益', key: '当期純利益', sortable: true, emphasized }
];

const PLTable = ({ data, mode }) => (
  <CommonTable
    data={data}
    mode={mode}
    sortable={mode === "year"}
    defaultSortKey="営業収入"
    theadCols={PLTheadCols}
    createTbody={createPLTbody}
  />
);

function createPLTbody(d, i, mode) {
  return (
    <Fragment>
      <TdHead d={d} i={i} mode={mode} />
      {PLTheadCols.map(obj => (
        <td key={obj.label} className={obj.emphasized ? 'em' : ''}>
          {d[obj.key]}
        </td>
      ))}
    </Fragment>
  );
}

export default PLTable;



