import React, { Fragment } from "react";
import CommonTable from "../ComTable";
import TdHead from "../TdHead";

const AttdTheadCols = [
  { label: "入場料収入", key: "入場料収入", sortable: true, emphasized: true },
  { label: "リーグ戦平均", key: "リーグ戦平均入場者数", sortable: true },
  { label: "年間入場者数", key: "総入場者数", sortable: true},
  { label: "ホーム試合数", key: "ホーム総試合数", sortable: true},
  { label: "客単価", key: "客単価", sortable: true }
];

const AttdTable = ({ data, mode }) => (
  <CommonTable
    data={data}
    mode={mode}
    sortable={mode === "year"}
    defaultSortKey="入場料収入"
    theadCols={AttdTheadCols}
    createTbody={createAttdTbody}
  />
);

function createAttdTbody(d, i, mode) {
  return (
    <Fragment>
      <TdHead d={d} i={i} mode={mode} />
      <td>{d['入場料収入']}</td>
      <td>{Math.round(d['リーグ戦平均入場者数'])}</td>
      <td>{d['総入場者数']}</td>
      <td>{d['ホーム総試合数'] || d['ホーム総入場者数']}</td>
      <td>{Math.round(d['客単価'])}</td>
    </Fragment>
  );
}

export default AttdTable;



