import React, { Fragment } from "react";

const TdHead = ({ d, i, type, mode }) =>
  type === "thead" ? (
    <Fragment>
      {mode === 'year' ? <td className="td-head">i</td> : null}
      <td className="td-head td-year">{mode === "year" ? "クラブ" : "年"}</td>
      <td className="td-head">所属</td>
      <td className="td-head">順位</td>
    </Fragment>
  ) : (
    <Fragment>
      {mode === 'year' ? <td className="td-head">{i + 1}</td> : null}
      <td className="td-head td-year">
        {mode === "year" ? d["クラブ"] : d["年"]}
      </td>
      <td className="td-head">{d["所属"]}</td>
      <td className="td-head">{d["順位"]}</td>
    </Fragment>
  );

export default TdHead;



