import React, { Fragment } from "react";
import CommonTable from "../ComTable";
import TdHead from "../TdHead";

const RevTheadCols = [
  { label: "営業収入", key: "営業収入", sortable: true, emphasized: true },
  { label: "広告料", key: "広告料収入", sortable: true },
  { label: "入場料", key: "入場料収入", sortable: true },
  { label: "配分金", key: "Jリーグ配分金", sortable: true },
  { label: "アカデミー関連", key: "アカデミー関連収入" },
  { label: "物販", key: "物販収入" },
  { label: "その他", key: "その他収入" }
];

const RevTable = ({ data, mode }) => (
  <CommonTable
    data={data}
    mode={mode}
    sortable={mode === "year"}
    defaultSortKey="営業収入"
    theadCols={RevTheadCols}
    createTbody={createRevTbody}
  />
);

function createRevTbody(d, i, mode) {
  const BasicRevs = () => (
    <Fragment>
      <TdHead d={d} i={i} mode={mode} />
      <td className="em">{d["営業収入"]}</td>
      <td>{d["広告料収入"]}</td>
      <td>{d["入場料収入"]}</td>
      <td>{d["Jリーグ配分金"]}</td>
    </Fragment>
  );

  return d["年"] < 2011 ? (
    <Fragment>
      <BasicRevs />
      <td colSpan={3}>{d["その他収入"]}</td>
    </Fragment>
  ) : d["年"] < 2016 ? (
    <Fragment>
      <BasicRevs />
      <td>{d["アカデミー関連収入"]}</td>
      <td colSpan={2}>{d["その他収入"]}</td>
    </Fragment>
  ) : (
    <Fragment>
      <BasicRevs />
      <td>{d["アカデミー関連収入"]}</td>
      <td>{d["物販収入"]}</td>
      <td>{d["その他収入"]}</td>
    </Fragment>
  );
}

export default RevTable;



