import React, { Fragment } from "react";
import CommonTable from "../ComTable";
import TdHead from "../TdHead";

const ExpTheadCols = [
  { label: "営業費用", key: "営業費用", sortable: true, emphasized: true },
  { label: "チーム人件費", key: "チーム人件費", sortable: true },
  { label: "試合関連", key: "試合関連経費"},
  { label: "トップチーム運営", key: "トップチーム運営経費"},
  { label: "アカデミー運営", key: "アカデミー運営経費" },
  { label: "女子チーム運営", key: "女子チーム運営経費" },
  { label: "物販関連", key: "物販関連費" },
  { label: "販売費", key: "販売費" },
  { label: "一般管理費", key: "一般管理費" }
];

const ExpTable = ({ data, mode }) => (
  <CommonTable
    data={data}
    mode={mode}
    sortable={mode === "year"}
    defaultSortKey="営業費用"
    theadCols={ExpTheadCols}
    createTbody={createExpTbody}
  />
);

function createExpTbody(d, i, mode) {

  return d['年'] < 2011 ? (
    <Fragment>
      <TdHead d={d} i={i} mode={mode} />
      <td className="em">{d['営業費用']}</td>
      <td>{d['チーム人件費']}</td>
      <td colSpan={6}>{d['事業費']}</td>
      <td colSpan={1}>{d['販売費および一般管理費']}</td>
    </Fragment>
  ) : d['年'] < 2016 ? (
    <Fragment>
      <TdHead d={d} i={i} mode={mode} />
      <td className="em">{d['営業費用']}</td>
      <td>{d['チーム人件費']}</td>
      <td>{d['試合関連経費']}</td>
      <td>{d['トップチーム運営経費']}</td>
      <td>{d['アカデミー運営経費']}</td>
      <td>{d['女子チーム運営経費']}</td>
      <td colSpan={3}>{d['販売費および一般管理費']}</td>
    </Fragment>
  ) : (
    <Fragment>
      <TdHead d={d} i={i} mode={mode} />
      <td className="em">{d['営業費用']}</td>
      <td>{d['チーム人件費']}</td>
      <td>{d['試合関連経費']}</td>
      <td>{d['トップチーム運営経費']}</td>
      <td>{d['アカデミー運営経費']}</td>
      <td>{d['女子チーム運営経費']}</td>
      <td>{d['物販関連費']}</td>
      <td colSpan={2}>{d['販売費および一般管理費']}</td>
    </Fragment>
  );
}

export default ExpTable;



