import React, { Fragment } from "react";
import CommonTable from "../ComTable";
import TdHead from "../TdHead";

const sortable = true;
const emphasized = true;
const BSTheadCols = [
  { label: "総資産", key: "総資産", sortable, emphasized },
  { label: "流動資産", key: "流動資産" },
  { label: "固定資産等", key: "固定資産等" },
  { label: "総負債", key: "総負債", sortable, emphasized },
  { label: "流動負債", key: "流動負債" },
  { label: "固定負債", key: "固定負債" },
  { label: "純資産", key: "純資産", sortable, emphasized },
  { label: "資本金", key: "資本金", sortable },
  { label: "資本剰余金等", key: "資本剰余金等" },
  { label: "利益剰余金", key: "利益剰余金" },
  { label: "当期純利益", key: "当期純利益", sortable }
];

const BSTable = ({ data, mode }) => (
  <CommonTable
    data={data}
    mode={mode}
    sortable={mode === "year"}
    defaultSortKey="純資産"
    theadCols={BSTheadCols}
    createTbody={createBSTbody}
  />
);

function createBSTbody(d, i, mode) {
  return (
    <Fragment>
      <TdHead d={d} i={i} mode={mode} />
      {BSTheadCols.map(obj => (
        <td key={obj.label} className={obj.emphasized ? "em" : ""}>
          {d[obj.key]}
        </td>
      ))}{/*
      <td>
        {d["流動資産"] && d["流動負債"]
          ? d3format(".0%")(d["流動資産"] / d["流動負債"])
          : ""}
      </td>
      <td>{d3format(".0%")(d["純資産"] / d["総資産"])}</td>*/}
    </Fragment>
  );
}

export default BSTable;



